import cheetah from "../../../assets/sprites/cheetah/cheetah.png";
import api from '../../../services/api';

const getUser = async () => {
    const _userId = sessionStorage.getItem('@pionira/userId');
    const res = await api.get(`/user/${_userId}`);

    return res.data.userName;

}


const cheetahBeggining = async () => {

    const userName = await getUser();
    const script = [
        {
            name: "Cheetah",
            image: cheetah,
            texts: [
                `Prazer ${userName}, eu sou eu sou Cheetah, o treinador mais rpido dessas bandas`, 
                'Somente os mais velozes conseguem me derrotar. Ser que um filhote como voc vai conseguir se equiparar a mim?',
                'Eu sou responsvel pela rea da Agilidade e vou te ensinar a no ficar para trs com a Ignorncia',
                'Para provar a mim que voc  veloz, ter que me enfrentar e passar pelo meu desafio. Assim, os desafios sempre vo estar disponveis, maaaaas se voc no estiver bem treinado sempre vai comer poeira, ento se esforce, treine,trabalhe duro e quem sabe um dia se torne digno(a) de combater a Ignorncia',
                'Ento filhote, voc deve estar ansioso para comear os desafios, mas sempre que voc tropear nessa corrida contra a Ignorncia ela vai conseguir ficar ainda mais na nossa frente. Ento antes de responder pense bem, no corra sem pensar, os filhotes devem pensar bem cada passo que forem dar, para um dia aprenderem a correr',
                'E o contrrio acontecer tambm: a cada sucesso a Ignorncia fica cada vez mais para trs. Um bom treino e at mais, filhote!'
            ]
        }
    ];

    return script;
}

export default cheetahBeggining;
