import monkey from "../../../assets/sprites/monkey/monkey.png";
import api from '../../../services/api';



const getUser = async () => {
    const _userId = sessionStorage.getItem('@pionira/userId');
    const res = await api.get(`/user/${_userId}`);

    return res.data.userName;

}


const mainPageScript = async () => {

    const userName = await getUser();
    const script = [
        {
            name: "Babuno",
            image: monkey,
            texts: [
                `Ol, jovem viajante. Seu nome  ${userName}, certo?`,
                'Eu sou o Babuno e preciso de sua ajuda. A Savana est sendo corrompida pela Ignorncia e o nico jeito de combat-la  aumentando a nossa Sabedoria.',
                'Fao parte do grupo Pioneiros e junto com outros somos responsveis por treinar viajantes com grande potencial que nem voc!',
                'Se desejar embarcar nessa aventura e nos ajudar a livrar a Savana da Ignorncia, v em frente e acesse uma das trilhas para conhecer os treinadores',
                'S tome cuidado com o covil da Mamba Negra, ela ser seu desafio final nessa jornada',
                'Antes de voc ir, gostaria de te dar algumas joias para te ajudar nesse comeo. Tenha uma boa viagem!'
            ]
        }
    ]

    return script;
}



export default mainPageScript;
