                'Ento filhote, j que  a sua primeira vez tentando encarar esse desafio eu vou te explicar sobre as Habilidades. Para conseguir derrotar de vez a Ignorncia  preciso se desenvolver em algumas reas da Sabedoria',
                'Eu sou responsvel pela rea da Agilidade e vou te ensinar a no ficar para trs com a Ignorncia. Ao longo do seu treinamento, voc receber pontos de habilidade em cada uma dessas reas e poder checar eles no seu passaporte',
                'Para provar a mim que voc  veloz, ter que me enfrentar e passar pelo meu desafio. Assim, os desafios sempre vo estar disponveis, maaaaas se voc no estiver bem treinado sempre vai comer poeira, ento se esforce, treine,trabalhe duro e quem sabe um dia se torne digno(a) de combater a Ignorncia',
